import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {FirebaseProvider} from "../../providers/firebase/firebase";
import {FirebaseListObservable} from "angularfire2/database";
import {Article} from "../../providers/model/article";
import {ArticleService} from "../../providers/article.service";
import {UserService} from "../../providers/user.service";

@IonicPage()
@Component({
  selector: 'page-purchase',
  templateUrl: 'purchase.html',
})
export class PurchasePage {

  type: string = "items";
  productPurchased: FirebaseListObservable<Article[]>;
  services = [];
  alive: boolean = true;

  constructor(public userService: UserService,
              public firebase: FirebaseProvider,
              private navCtrl: NavController,
              private articleService: ArticleService) {

    this.productPurchased = this.articleService.getMyPurchases(this.userService._userFirebase.uid);
  }

  ionViewDidLoad() {
    this.articleService.getMyServiceOrders(this.userService._userFirebase.uid)
      .takeWhile(() => this.alive)
      .subscribe(services => {
      services.forEach(service => {
        this.articleService.getServiceFromId(service.serviceId).subscribe(data => {
          if(data) {
            service.service = data;
            this.services.push(service);
          }
        });
      })
    });
  }

  ionViewDidLeave() {
    this.alive = false;
  }

  seeDetails(item, key, isInventory) {
    if(isInventory) {
      this.navCtrl.push('ArticlePage',
        {article: item, mine: true, isInventory: isInventory});
    } else {
      this.navCtrl.push('ArticlePage',
        {article: item, key: key, mine: true});
    }
  }

  seeDetailsService(item) {
    this.navCtrl.push('ServicePage',
      {service: item.service, key: item.id ? item.id : item.$key, serviceOrdered: item});
  }

}



